.page-header {
  margin-bottom: 24px;
}

.page-header__top-bar--opened {
  position: relative;
}

.page-header__top-bar--nojs .page-header__toggle {
  display: none;
}

.page-header__top-bar--opened .main-nav__list--header {
  @media (max-width: $desktop-width) {
    position: absolute;
  }
}

.page-header__top-bar--closed .page-header__main-nav {
  @media (max-width: $desktop-width) {
    display: none;
  }
}

.page-header__logo {
  margin-left: 68px;
  margin-top: 10px;
  margin-bottom: 20px;
  cursor: pointer;
  display: inline-block;
}


.page-header__toggle {
  cursor: pointer;
}
