.offer__slider-header {
  font-family: "Amatic SC", cursive;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin: 0;

  &--salad {
    color: $salad-600;
  }

  &--italian {
    color: $green-500;
  }

  &--red {
    color: $red-500;
  }

  &--eastern {
    color: $brown-500;
  }
}

.offer__nav-bar {
  list-style: none;
  margin: 0;
  padding: 0;
}

.offer__nav-item {
  width: 143px;
}

.offer__nav-link {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  border: 1px solid;
  display: block;
  padding: 8px 16px;
}

.offer__nav-bar--salad .offer__nav-link {
  color: $salad-200;
  border-color: $salad-200;
}

.offer__nav-bar--italian .offer__nav-link {
  color: $green-200;
  border-color: $green-200;
}

.offer__nav-bar--meat .offer__nav-link {
  color: $red-300;
  border-color: $red-300;
}

.offer__nav-bar--eastern .offer__nav-link {
  color: $brown-400;
  border-color: $brown-400;
}

.offer__nav-link--active {
  border: none;
}

.offer__nav-link--active.offer__nav-link--salad {
  color: $salad-800;
  background-color: $salad-200;
}

.offer__nav-link--active.offer__nav-link--italian {
  color: $green-800;
  background-color: $green-200;
}

.offer__nav-link--active.offer__nav-link--meat {
  color: $red-900;
  background-color: $red-300;
}

.offer__nav-link--active.offer__nav-link--eastern {
  color: $brown-700;
  background-color: $brown-300;
}

.offer__slider-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.offer__slider-item {
  display: none;

  &--active {
    display: block;
  }
}

.offer__slider-item-header {
  font-family: "Amatic SC", cursive;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin: 0;
}

.offer__slider-text {
  font-weight: 300;
  margin: 0;
}

.offer__slider-quantity-buttons {
  margin: 0;
}

.offer__slider-quantity-button {
  width: 32px;
  height: 32px;
}

.offer__slider-output {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;

  &--salad {
    color: $salad-600;
  }

  &--italian {
    color: $green-500;
  }

  &--meat {
    color: $red-500;
  }

  &--eastern {
    color: $brown-500;
  }
}

.offer__slider-control {
  display: none;
}
